/**
 * Card component
 * 
 * A flexible card component for displaying content in a contained box
 * 
 * @param {React.ReactNode} children - Card content
 * @param {string} className - Additional CSS classes
 * @param {boolean} hover - Enable hover animation
 * @param {object} props - Additional HTML div attributes
 */

import { motion } from 'framer-motion';

interface CardProps extends React.HTMLAttributes<HTMLDivElement> {
  children: React.ReactNode;
  className?: string;
  hover?: boolean;
}

const Card = ({ children, className = '', hover = false, ...props }: CardProps) => {
  const baseStyles = 'bg-white dark:bg-[var(--primary-dark)]/30 rounded-2xl shadow-md overflow-hidden border border-gray-100 dark:border-gray-800';
  const cardClasses = `${baseStyles} ${className}`;
  
  const variants = {
    hidden: { opacity: 0, y: 20 },
    visible: { 
      opacity: 1, 
      y: 0,
      transition: {
        duration: 0.5,
        ease: "easeOut"
      }
    }
  };

  return hover ? (
    <motion.div
      className={cardClasses}
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, margin: "-100px" }}
      variants={variants}
      whileHover={{ 
        y: -10, 
        boxShadow: "0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)"
      }}
      {...props}
    >
      {children}
    </motion.div>
  ) : (
    <motion.div
      className={cardClasses}
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, margin: "-100px" }}
      variants={variants}
      {...props}
    >
      {children}
    </motion.div>
  );
};

export default Card;