/**
 * TestimonialsSection component
 * 
 * Displays client testimonials in a carousel format
 */

'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';
import { motion, AnimatePresence } from 'framer-motion';
import { FaQuoteLeft, FaChevronLeft, FaChevronRight, FaStar } from 'react-icons/fa';
import Container from '../ui/Container';
import Section from '../ui/Section';
import Card from '../ui/Card';

// Sample testimonials data
const testimonials = [
  {
    id: 1,
    name: 'Sarah Johnson',
    role: 'CEO, TechStart',
    image: 'https://randomuser.me/api/portraits/women/1.jpg',
    quote: 'Working with Jcustoms was a game-changer for our business. They transformed our outdated website into a modern, user-friendly platform that increased our conversions by 40%.',
    rating: 5,
  },
  {
    id: 2,
    name: 'Michael Chen',
    role: 'Marketing Director, GrowthBrand',
    image: 'https://randomuser.me/api/portraits/men/2.jpg',
    quote: 'I was blown away by the attention to detail and creative solutions provided by Jcustoms. They didn&apos;t just build a website; they created a digital experience that our customers love.',
    rating: 5,
  },
  {
    id: 3,
    name: 'Emma Rodriguez',
    role: 'Founder, StyleShop',
    image: 'https://randomuser.me/api/portraits/women/3.jpg',
    quote: 'The e-commerce platform Jcustoms built for us is beautiful and incredibly functional. Our sales have increased significantly, and the mobile experience is seamless.',
    rating: 5,
  },
  {
    id: 4,
    name: 'David Kim',
    role: 'CTO, HealthTech Solutions',
    image: 'https://randomuser.me/api/portraits/men/4.jpg',
    quote: 'Jcustoms understood our complex requirements and delivered a robust web application that exceeded our expectations. Their technical expertise is truly impressive.',
    rating: 5,
  },
];

const TestimonialsSection = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [direction, setDirection] = useState(0);
  const [autoplay, setAutoplay] = useState(true);

  // Handle next/previous testimonial
  const handleNext = () => {
    setDirection(1);
    setCurrentIndex((prevIndex) => (prevIndex + 1) % testimonials.length);
  };

  const handlePrev = () => {
    setDirection(-1);
    setCurrentIndex((prevIndex) => (prevIndex - 1 + testimonials.length) % testimonials.length);
  };

  // Autoplay functionality
  useEffect(() => {
    if (!autoplay) return;
    
    const interval = setInterval(() => {
      handleNext();
    }, 5000);

    return () => clearInterval(interval);
  }, [currentIndex, autoplay]);

  // Pause autoplay on hover
  const handleMouseEnter = () => setAutoplay(false);
  const handleMouseLeave = () => setAutoplay(true);

  // Animation variants for slide transitions
  const slideVariants = {
    enter: (direction: number) => ({
      x: direction > 0 ? '100%' : '-100%',
      opacity: 0
    }),
    center: {
      x: 0,
      opacity: 1
    },
    exit: (direction: number) => ({
      x: direction > 0 ? '-100%' : '100%',
      opacity: 0
    })
  };

  return (
    <Section id="testimonials" bg="accent">
      <Container>
        <div className="text-center mb-16">
          <motion.h2
            className="text-3xl md:text-4xl font-bold mb-4"
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5 }}
          >
            Client <span className="text-[var(--primary)]">Testimonials</span>
          </motion.h2>
          <motion.p
            className="text-lg text-gray-600 dark:text-gray-300 max-w-2xl mx-auto"
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            Hear what our clients have to say about their experience working with us.
          </motion.p>
        </div>

        {/* Testimonials carousel */}
        <div 
          className="max-w-4xl mx-auto relative"
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          <AnimatePresence mode="popLayout" initial={false} custom={direction}>
            <motion.div
              key={currentIndex}
              custom={direction}
              variants={slideVariants}
              initial="enter"
              animate="center"
              exit="exit"
              transition={{ duration: 0.5, ease: "easeInOut" }}
              className="w-full"
            >
              <Card className="p-8 md:p-10">
                <div className="flex flex-col md:flex-row gap-8 items-center">
                  <div className="shrink-0">
                    <div className="w-24 h-24 rounded-full overflow-hidden border-4 border-[var(--primary-light)]">
                      <Image 
                        src={testimonials[currentIndex].image} 
                        alt={testimonials[currentIndex].name}
                        width={96}
                        height={96}
                        className="w-full h-full object-cover"
                      />
                    </div>
                  </div>

                  <div className="flex-1">
                    <FaQuoteLeft className="text-[var(--primary-light)] opacity-20 text-4xl mb-4" />
                    <p className="text-lg italic mb-6">
                      &quot;{testimonials[currentIndex].quote}&quot;
                    </p>

                    <div className="flex items-center gap-1 mb-3">
                      {[...Array(testimonials[currentIndex].rating)].map((_, i) => (
                        <FaStar key={i} className="text-[var(--accent)]" />
                      ))}
                    </div>

                    <h3 className="font-bold text-xl">{testimonials[currentIndex].name}</h3>
                    <p className="text-gray-600 dark:text-gray-400">{testimonials[currentIndex].role}</p>
                  </div>
                </div>
              </Card>
            </motion.div>
          </AnimatePresence>

          {/* Navigation buttons */}
          <button
            onClick={handlePrev}
            className="absolute left-2 top-1/2 -translate-y-1/2 w-10 h-10 bg-white dark:bg-[var(--primary-dark)] rounded-full flex items-center justify-center shadow-md z-10 focus:outline-none hover:bg-gray-50 dark:hover:bg-[var(--primary-dark)]/70"
            aria-label="Previous testimonial"
          >
            <FaChevronLeft />
          </button>
          <button
            onClick={handleNext}
            className="absolute right-2 top-1/2 -translate-y-1/2 w-10 h-10 bg-white dark:bg-[var(--primary-dark)] rounded-full flex items-center justify-center shadow-md z-10 focus:outline-none hover:bg-gray-50 dark:hover:bg-[var(--primary-dark)]/70"
            aria-label="Next testimonial"
          >
            <FaChevronRight />
          </button>

          {/* Pagination indicators */}
          <div className="flex justify-center mt-8 gap-2">
            {testimonials.map((_, index) => (
              <button
                key={index}
                onClick={() => {
                  setDirection(index > currentIndex ? 1 : -1);
                  setCurrentIndex(index);
                }}
                className={`w-3 h-3 rounded-full transition-all ${
                  index === currentIndex
                    ? 'bg-[var(--primary)] w-6'
                    : 'bg-gray-300 dark:bg-gray-600'
                }`}
                aria-label={`Go to testimonial ${index + 1}`}
              />
            ))}
          </div>
        </div>
      </Container>
    </Section>
  );
};

export default TestimonialsSection;