/**
 * MobileMenu component
 * 
 * Responsive mobile navigation menu that slides in from the right
 */

'use client';

import { useRef, useEffect } from 'react';
import Link from 'next/link';
import { motion } from 'framer-motion';
import Button from '../ui/Button';

const navLinks = [
  { href: '#home', label: 'Home' },
  { href: '#services', label: 'Services' },
  { href: '#portfolio', label: 'Portfolio' },
  { href: '#testimonials', label: 'Testimonials' },
  { href: '#about', label: 'About' },
];

interface MobileMenuProps {
  onClose: () => void;
}

const MobileMenu = ({ onClose }: MobileMenuProps) => {
  const menuRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleOutsideClick = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        onClose();
      }
    };

    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        onClose();
      }
    };

    // Add event listeners
    document.addEventListener('mousedown', handleOutsideClick);
    document.addEventListener('keydown', handleEscape);

    // Clean up event listeners
    return () => {
      document.removeEventListener('mousedown', handleOutsideClick);
      document.removeEventListener('keydown', handleEscape);
    };
  }, [onClose]);

  // Animation variants
  const menuVariants = {
    hidden: { x: '100%', opacity: 0 },
    visible: { 
      x: 0, 
      opacity: 1,
      transition: {
        type: 'spring',
        damping: 25,
        stiffness: 250
      }
    },
    exit: { 
      x: '100%', 
      opacity: 0,
      transition: {
        type: 'spring',
        damping: 30,
        stiffness: 300
      }
    }
  };

  const linkVariants = {
    hidden: { opacity: 0, y: 10 },
    visible: (custom: number) => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: custom * 0.1,
        duration: 0.3,
        ease: 'easeOut'
      }
    })
  };

  return (
    <motion.div 
      className="fixed inset-0 bg-black/30 backdrop-blur-sm z-40"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <motion.div 
        ref={menuRef}
        className="absolute top-0 right-0 bottom-0 w-[80%] max-w-sm bg-white dark:bg-[var(--background)] shadow-xl py-6 px-6 flex flex-col"
        variants={menuVariants}
        initial="hidden"
        animate="visible"
        exit="exit"
      >
        <nav className="mt-12">
          <ul className="flex flex-col gap-4">
            {navLinks.map((link, index) => (
              <motion.li 
                key={link.href}
                custom={index}
                variants={linkVariants}
                initial="hidden"
                animate="visible"
              >
                <Link 
                  href={link.href} 
                  onClick={onClose}
                  className="text-lg font-medium text-[var(--foreground)] hover:text-[var(--primary)] block py-2 border-b border-gray-100 dark:border-gray-800"
                >
                  {link.label}
                </Link>
              </motion.li>
            ))}
          </ul>
        </nav>
        
        <div className="mt-auto">
          <Button variant="primary" fullWidth className="mb-4">
            Contact Us
          </Button>
        </div>
      </motion.div>
    </motion.div>
  );
};

export default MobileMenu;