/**
 * Container component
 * 
 * A responsive container component to constrain content width
 * 
 * @param {React.ReactNode} children - Container content
 * @param {string} className - Additional CSS classes
 * @param {boolean} fluid - Whether the container should be full-width or constrained
 * @param {object} props - Additional HTML div attributes
 */

interface ContainerProps extends React.HTMLAttributes<HTMLDivElement> {
  children: React.ReactNode;
  className?: string;
  fluid?: boolean;
}

const Container = ({ children, className = '', fluid = false, ...props }: ContainerProps) => {
  const containerClasses = `
    w-full mx-auto px-4 sm:px-6 
    ${fluid ? '' : 'max-w-7xl'}
    ${className}
  `;

  return (
    <div className={containerClasses} {...props}>
      {children}
    </div>
  );
};

export default Container;