/**
 * Section component
 * 
 * A component for creating page sections with consistent spacing
 * 
 * @param {React.ReactNode} children - Section content
 * @param {string} className - Additional CSS classes
 * @param {string} id - Section ID for navigation
 * @param {string} bg - Background color or style (default, primary, secondary, accent, dark)
 * @param {object} props - Additional HTML section attributes
 */

interface SectionProps extends React.HTMLAttributes<HTMLElement> {
  children: React.ReactNode;
  className?: string;
  id?: string;
  bg?: 'default' | 'primary' | 'secondary' | 'accent' | 'dark';
}

const Section = ({ children, className = '', id, bg = 'default', ...props }: SectionProps) => {
  const baseStyles = 'section-padding relative overflow-hidden';
  
  const bgStyles = {
    default: 'bg-white dark:bg-[var(--background)]',
    primary: 'bg-[var(--primary-light)]/10 dark:bg-[var(--primary-dark)]',
    secondary: 'bg-[var(--secondary-light)]/10 dark:bg-[var(--secondary-dark)]',
    accent: 'bg-[var(--accent-light)]/10 dark:bg-[var(--accent-dark)]',
    dark: 'bg-gray-900 text-white dark:bg-[var(--background)]'
  };
  
  const sectionClasses = `${baseStyles} ${bgStyles[bg]} ${className}`;

  return (
    <section id={id} className={sectionClasses} {...props}>
      {children}
    </section>
  );
};

export default Section;