/**
 * Navigation component
 * 
 * Main navigation links for desktop view
 */

'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { motion } from 'framer-motion';
import Button from '../ui/Button';

interface NavigationProps {
  className?: string;
}

const navLinks = [
  { href: '#home', label: 'Home' },
  { href: '#services', label: 'Services' },
  { href: '#portfolio', label: 'Portfolio' },
  { href: '#testimonials', label: 'Testimonials' },
  { href: '#about', label: 'About' },
];

const Navigation = ({ className = '' }: NavigationProps) => {
  const [activeSection, setActiveSection] = useState('home');

  useEffect(() => {
    const handleScroll = () => {
      const sections = navLinks.map(link => link.href.substring(1));
      
      for (const section of sections.reverse()) {
        const element = document.getElementById(section);
        if (element) {
          const rect = element.getBoundingClientRect();
          if (rect.top <= 100) {
            setActiveSection(section);
            break;
          }
        }
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <nav className={`items-center gap-1 ${className}`}>
      <ul className="flex items-center gap-2">
        {navLinks.map((link) => (
          <li key={link.href}>
            <Link href={link.href} passHref legacyBehavior>
              <motion.a
                className={`px-3 py-2 relative rounded-md text-sm font-medium transition-all ${
                  activeSection === link.href.substring(1)
                    ? 'text-[var(--primary)]'
                    : 'text-[var(--foreground)] hover:text-[var(--primary)]'
                }`}
                whileHover={{ y: -2 }}
                whileTap={{ y: 0 }}
              >
                {link.label}
                {activeSection === link.href.substring(1) && (
                  <motion.span
                    className="absolute bottom-0 left-0 right-0 h-[3px] bg-[var(--primary)] rounded-full mx-3"
                    layoutId="navbar-indicator"
                    transition={{ type: 'spring', stiffness: 300, damping: 30 }}
                  />
                )}
              </motion.a>
            </Link>
          </li>
        ))}
      </ul>
      <Button size="sm" variant="primary" className="ml-4">
        Contact Us
      </Button>
    </nav>
  );
};

export default Navigation;