/**
 * Button component
 * 
 * A customizable button component with different variants and sizes
 * 
 * @param {string} variant - The button style variant (primary, secondary, outline, ghost)
 * @param {string} size - The button size (sm, md, lg)
 * @param {boolean} fullWidth - Whether the button should take full width
 * @param {React.ReactNode} children - Button content
 * @param {React.ButtonHTMLAttributes<HTMLButtonElement>} props - Any other button props
 */

import { forwardRef } from 'react';
import { motion } from 'framer-motion';

type ButtonVariant = 'primary' | 'secondary' | 'outline' | 'ghost';
type ButtonSize = 'sm' | 'md' | 'lg';

interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: ButtonVariant;
  size?: ButtonSize;
  fullWidth?: boolean;
  children: React.ReactNode;
}

const Button = forwardRef<HTMLButtonElement, ButtonProps>(
  ({ variant = 'primary', size = 'md', fullWidth = false, children, className = '', ...props }, ref) => {
    const baseStyles = 'rounded-full font-medium inline-flex items-center justify-center transition-all relative overflow-hidden';
    
    const variantStyles = {
      primary: 'bg-[var(--primary)] text-white hover:bg-[var(--primary-light)] focus:ring-2 focus:ring-[var(--primary-light)] focus:ring-offset-2',
      secondary: 'bg-[var(--secondary)] text-white hover:bg-[var(--secondary-light)] focus:ring-2 focus:ring-[var(--secondary-light)] focus:ring-offset-2',
      outline: 'border-2 border-[var(--primary)] text-[var(--primary)] bg-transparent hover:bg-[var(--primary)] hover:text-white focus:ring-2 focus:ring-[var(--primary-light)]',
      ghost: 'bg-transparent text-[var(--foreground)] hover:bg-black/5 dark:hover:bg-white/10 focus:ring-2 focus:ring-[var(--foreground)]/20'
    };
    
    const sizeStyles = {
      sm: 'text-sm py-1.5 px-3',
      md: 'text-base py-2.5 px-5',
      lg: 'text-lg py-3 px-6'
    };
    
    const widthStyles = fullWidth ? 'w-full' : '';
    
    const buttonClasses = `${baseStyles} ${variantStyles[variant]} ${sizeStyles[size]} ${widthStyles} ${className}`;
    
    return (
      <motion.button
        ref={ref}
        className={buttonClasses}
        whileTap={{ scale: 0.95 }}
        whileHover={variant === 'primary' || variant === 'secondary' ? { y: -3, boxShadow: '0 10px 20px rgba(0,0,0,0.2)' } : { y: -3 }}
        {...props}
      >
        {children}
      </motion.button>
    );
  }
);

Button.displayName = 'Button';

export default Button;