/**
 * Logo component
 * 
 * The site logo with animation effects
 */

'use client';

import Link from 'next/link';
import { motion } from 'framer-motion';

const Logo = () => {
  return (
    <Link href="/" className="inline-block">
      <motion.div 
        className="font-bold text-2xl flex items-center"
        initial={{ opacity: 0, y: -10 }}
        animate={{ opacity: 1, y: 0 }}
        whileHover={{ scale: 1.05 }}
        transition={{ duration: 0.3 }}
      >
        <span className="text-[var(--primary)]">J</span>
        <span className="text-[var(--secondary)]">customs</span>
        <span className="ml-1 text-xs font-normal bg-[var(--accent)] rounded px-1 py-0.5 text-black">Web</span>
      </motion.div>
    </Link>
  );
};

export default Logo;